.TaskList {
  width: 650px;
  padding: 0;
  margin: 0;
  margin-left: auto;
  margin-right: auto;
}

.TaskList_item {
  display: flex;
  align-items: center;
  padding: 12px;
  background-color: #6b5b95;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  border-radius: 5px;
}

.TaskList_item + .TaskList_item {
  margin-top: 12px;
  background-color: #6b5b95;
  border-radius: 5px;
}

.TaskList_text {
  flex: 0 0 65%;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 16px;
  border-radius: 3px;
}

.TaskList_actions {
  display: flex;
  justify-content: center;
}

.TaskList_button {
  color: #fff;
  margin-right: 4px;
  margin-left: auto;
  background-color: #878f99;
  border-radius: 50px;
  width: 75px;
  border: 0;
  outline: 0;
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.25);
}
