.Serch {
  padding-right: 20px;
  padding-left: 160px;
}

.SerchInput {
  color: #171718;
  font: inherit;
  font-size: 1.2rem;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #bdbdbd;
  width: 620px;
  outline: 0;
  /* margin-left: 160px; */
  margin-right: auto;
}
.Serch label {
  padding-left: auto;
  margin-left: auto;
  /* margin-right: auto; */
}
.Download {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
  outline: 0;
  border-radius: 50px;
  height: 40px;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: #6b5b95;
  width: 250px;
  text-decoration: none;
  color: #fff;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}
