.TaskEditor {
  display: flex;
  flex-direction: column;
  padding: 25px;
  width: 600px;
  border-radius: 5px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.94), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  margin-bottom: 16px;
  margin-left: 45px;
  background-color: #6b5b95;
  margin-left: auto;
  margin-right: auto;
}

.TaskEditor_label {
  display: inline-block;
  margin-bottom: 16px;
  font-size: 20px;
  color: #171718;
  cursor: pointer;
  margin-right: 5px;
  margin-left: auto;
}

.TaskEditor_input {
  color: #171718;
  font: inherit;
  font-size: 1.2rem;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #bdbdbd;
  width: 450px;
  outline: 0;
  margin-left: 10px;
  margin-right: 25px;
}

.TaskEditorinput:focus {
  border: 1px solid #2b32b2;
}

.TaskEditor_button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #878f99;
  border: 0;
  outline: 0;
  padding: 8px 24px;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.25);
  font: inherit;
  text-transform: uppercase;
  user-select: none;
  transition: background-color 200ms ease-in-out;
}
text {
  margin-left: 20px;
  margin-right: auto;
}
